

*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}


.navbar{

    height:50px;
    background-color: #003580;
    display:flex;
    justify-content: center;
}

.navcontainer{

    width:100%;
    max-width: 1024px;
    color:white;
    display:flex;
    align-items: center;
    justify-content: space-between;
}

.logo{

    font-weight: 600;  
}

.navbutton{

    margin-left: 20px;
    border:none;
    padding:5px 10px;
    cursor:pointer;
    color:#003580;
}


@media screen and (max-width:1024px) {

    /* .navbar{

        background-color: red;
        flex-direction: column;
        height:max-content;
    } */

    .navcontainer{

        /* height:max-content; */
        flex-direction: column;
    }
    
}