.mail{

    width:100%;
    margin-top: 50px;
    background-color: #003580;
    color:white;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:20px;
    padding:50px;
}


.mailinputcont>input{

    width:300px;
    height:30px;
    padding:10px;
    border:none;
    margin-right: 10px;
    border-radius: 5px;

}

.mailinputcont>button{

    height:30px;
    padding:5px;
    background-color: #0071c2;
    color:white;
    border:none;
    font-weight: 500;
    cursor: pointer;
    border-radius: 5px;
    
}